import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import SecurityIcon from '@mui/icons-material/Security';
import CardGiftcardIcon from '@mui/icons-material/CardGiftcard';
import '../Components.css';

function Features() {
    return (
        <div style={{backgroundColor:'#d1c4e9'}}>
        <Container>
            <h1 style={{ margin: '2% auto', paddingTop: '3%', textAlign: 'center', color: 'white' }}> ?    </h1>
            <Row>
                <Col xs={12} md={4}>
                    <Row>
                        <Col xs={12}>
                            <div style={{ marginBottom: '15%' }}>
                                <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '1%' }}>
                                    <LocationOnIcon className="features-icon" sx={{ width: '15%', height: '15%' }} />
                                </div>
                                <div className="features-text">
                                    <div>   </div>
                                    <div>   3    !
                                            400</div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Col>
                <Col xs={12} md={4}>
                    <Row>
                        <Col xs={12}>
                            <div style={{ marginBottom: '15%' }}>
                                <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '1%' }}>
                                    <SecurityIcon className="features-icon" sx={{ width: '15%', height: '15%' }}/>
                                </div>
                                <div className="features-text">
                                    <div> </div>
                                    <div>    SSL.
                                               .</div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Col>
                <Col xs={12} md={4}>
                    <Row>
                        <Col xs={12}>
                            <div style={{ marginBottom: '15%' }}>
                                <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '1%' }}>
                                    <CardGiftcardIcon className="features-icon" sx={{ width: '15%', height: '15%' }}/>
                                </div>
                                <div className="features-text">
                                    <div>   </div>
                                    <div>   !        .</div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
        </div>
    )
}

export default Features